<template>
  <article class="modal shopping-cart">
    <div class="modal__wrapper">
      <div class="modal__container">
        <form class="modal__content" acrtion="" id="form" @submit.prevent="buyProducts">
          <div class="modal__col">
            <div class="modal__named">
              <h3 class="modal__model">
                Ваш
              </h3>
              <h1 class="modal__title">
                заказ
              </h1>
            </div>
            <div class="modal__description">
              <label for="name">Ваше имя</label> <span><input type="text" id="name" placeholder="имя" v-model="form.name" required> <input type="text" id="last_name" v-model="form.last_name" placeholder="фамилия" required></span>
              <label for="phone">Телефон</label> <span><input type="tel" id="phone" v-model="form.phone" placeholder="+38 (0**) *******" minlength="9" maxlength="14" required></span>
              <label for="mail">E-mail</label> <span><input type="mail" id="mail" v-model="form.mail" placeholder="example@yourmail.com" required></span>
              <label for="pay">Способ оплаты</label>
              <span>
                <select id="pay" @change="showSubInfo" v-model="form.paymentMethod">
                   <option disabled>Выберите способ оплаты</option>
                   <option value="nova_poshta">Наложенный платеж "Нова Пошта" (при получении)</option>
                   <option value="zabery_sam" selected>Заберу сам</option>
                 </select>
              </span>

              <label for="area" v-if="visibleSubInfo">Область</label> <span v-if="visibleSubInfo"><input type="text" id="area" v-model="form.area" required></span>
              <label for="town" v-if="visibleSubInfo">Город</label> <span v-if="visibleSubInfo"><input type="text" id="town" v-model="form.town" required></span>
              <label for="number" v-if="visibleSubInfo">Номер отделения</label> <span v-if="visibleSubInfo"><input type="text" v-model="form.numberDepartment" id="number" required></span>
            </div>
            <div class="modal__btn">
                <button type="submit">
                  подтвердить
                </button>
            </div>
          </div>
          <div class="modal__col">
            <div class="modal__description modal__input_cases">
              <p>Выбранные позиции</p>

              <ul class="shopping__list">
                <li v-for="index in shoppingList" class="shopping__list--item">
                  <div class="shopping__list--col">
                    <div class="shopping__list--img-wrapper">
                      <div class="shopping__list--img" :style="{ 'background-image': 'url(' + index.img + ')' }"></div>
                    </div>
                    <div>
                      <div class="shopping__list--phone">
                        {{ index.type }} {{ index.modelPhone }}
                      </div>
                      <div class="shopping__list--model">
                        {{ index.modelCase }}
                      </div>
                    </div>
                  </div>
                  <div class="shopping__list--col">
                    <div class="shopping__list--count">
                      <div class="shopping__list--count-wrapper">
                        <button @click.prevent="countMinus(index)" :style="{ 'background-image': 'url(img/minus.svg)' }"></button>
                        <span>{{ index.count }}</span>
                        <button @click.prevent="countPlus(index)" :style="{ 'background-image': 'url(img/plus.svg)' }"></button>
                      </div>
                    </div>
                  </div>
                  <div class="shopping__list--col">
                    <div class="shopping__list--price">
                      {{ index.price }}<span class="shopping__list--price-text">грн.</span>
                    </div>
                    <div class="shopping__list--price-info">
                      {{ index.count }}шт. x {{ index.priceOneCount }}грн.
                    </div>
                  </div>
                  <div class="shopping__list--count shopping__list--delete" >
                    <button :style="{ 'background-image': 'url(img/plus.svg)' }" @click.prevent="deleteElementByShoppingList(index)"></button>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </form>
      </div>
      <div class="modal__footer">
        <div class="modal__back">
          <router-link to="/"  tag="a" :exact="true" alt="title">
            <img src="img/goBack.png" alt="">
          </router-link>
        </div>
      </div>
    </div>

    <div class="modal_response" v-if="serverAnswer">
      <div class="modal_response__content">
        <div class="modal_response__content--wrapper">
          <div class="col-6">
            {{ form }}
          </div>
          <div class="col-6">
            {{ sendList }}
          </div>
        </div>
        <button class="closeModal" @click.prevent="closeModal">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="#fff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-plus"><line x1="12" y1="5" x2="12" y2="19"></line><line x1="5" y1="12" x2="19" y2="12"></line></svg>
        </button>
      </div>
    </div>
  </article>
</template>

<script>
  import store from '../../store.js';

  export default {
    name: "ShoppingCartList",
    data() {
      return {
        visibleSubInfo: false,
        mobile_case: [
          {
            id: 'dark_black',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black',
            size_price: [
              {
                model: 'XS',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              },
              {
                model: '8',
                size: '5.5',
                price: '225'
              },
              {
                model: '7 plus',
                size: '6.3',
                price: '175'
              },
              {
                model: '7',
                size: '4.8',
                price: '150'
              }
            ]
          },
          {
            id: 'deep_brown',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'bright yellow',
            size_price: [
              {
                model: 'Xr',
                size: '6.5',
                price: '325'
              },
              {
                model: 'Xs',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'brown',
            img: 'img/case/brown.png',
            type: 'apple iphone',
            model: 'deep brown',
            size_price: [
              {
                model: 'X',
                size: '6.5',
                price: '325'
              },
              {
                model: 'Xs',
                size: '6.1',
                price: '275'
              },
              {
                model: 'Xr',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'dark',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black',
            size_price: [
              {
                model: 'Xs',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'deep',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'deep brown',
            size_price: [
              {
                model: 'XS',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
        ],
        shoppingList: [],
        storageList: null,
        sendList: [],
        serverAnswer: false,
        form: {
          name: null,
          last_name: null,
          phone: null,
          mail: null,
          paymentMethod: 'Выберите способ оплаты',
          town: null,
          area: null,
          numberDepartment: null,
        },
    }
    },
    created() {
      let vm = this;
      vm.storageList = store.getStore();

      // console.log( store.getStore() )
      for(let item in vm.mobile_case ) {
        for( let key in vm.storageList ) {
          // console.log( item.size_price )
          for(let index in vm.mobile_case[item].size_price) {
            if( vm.mobile_case[item].id == vm.storageList[key].id &&
                vm.mobile_case[item].size_price[index].model == vm.storageList[key].model ) {
              let arr = {
                id: vm.mobile_case[item].id,
                img: vm.mobile_case[item].img,
                type: vm.mobile_case[item].type,
                modelCase: vm.mobile_case[item].model,
                modelPhone: vm.storageList[key].model,
                // size: vm.mobile_case[item].size_price[index].size,
                price: vm.mobile_case[item].size_price[index].price,
                priceOneCount: vm.mobile_case[item].size_price[index].price,
                count: 1
              };
              vm.shoppingList.push( arr )
            }
          }
        }
      };



      console.log( vm.shoppingList );
    },
    methods: {
      showSubInfo(e) {
        if( this.form.paymentMethod ==  'nova_poshta') {
          return this.visibleSubInfo = true;
        } else {
          return this.visibleSubInfo = false;
        }
      },
      countPlus(item) {
        item.price = ++item.count * item.priceOneCount;
      },
      countMinus(item) {
        if(item.count > 1)
          item.price = --item.count * item.priceOneCount;
      },
      deleteElementByShoppingList(item) {
        store.deleteElement( item.id, item.modelPhone  );
        this.shoppingList = this.shoppingList.filter(function (el) {
          if( item.id != el.id || item.modelPhone != el.modelPhone ) {
            return el;
          }
        });
        if( !this.shoppingList.length ) {
          this.$router.push('/');
        }
      },
      buyProducts() {
        for(let key in this.shoppingList) {
          let arr = {
            count: this.shoppingList[key].count,
            id: this.shoppingList[key].id,
            modelCase: this.shoppingList[key].modelCase,
            modelPhone: this.shoppingList[key].modelPhone,
            price: this.shoppingList[key].price,
            priceOneCount: this.shoppingList[key].priceOneCount,
            type: this.shoppingList[key].type,
          };
          this.sendList.push( arr );
        };

        this.serverAnswer = true;
        console.log( this.sendList );
        console.log( this.form );
      },
      closeModal() {
        this.serverAnswer = false;
      },
    }
  }
</script>

<style lang="scss">
  .shopping-cart.modal {
    .modal {
      &__content {
        @media screen and (max-width: 1200px) {
          flex-direction: column;
          align-items: center;
        }
      }
      &__model {
        color: #ff0000;
        bottom: 7.5rem;
        left: -42%;
        z-index: 0;
        @media screen and (max-width: 992px) {
          bottom: 4.75rem;
        }
      }
      &__col {
        &:first-child {
          align-items: center;
          @media screen and (max-width: 1200px) {
            margin: 0;
            width: 100%;
          }
        }
        &:last-child {
          /*margin-right: 10%;*/
          .modal {
            &__description {
              display: inline-block;
              /*width: 96%;*/
              width: 100%;
            }
          }
        }
      }
      &__btn {
        button {
          background-color: #e40000;
          padding: 1.25rem 1.75rem;
          opacity: .7;
          transition: all ease-in-out .3s;
          @media screen and (max-width: 1200px) {
            margin: 1.5rem 0;
          }
          &:hover {
            opacity: 1;
          }
        }
      }
      &__description {
        font-family: Oswald;
        font-size: 2rem;
        font-weight: 500;
        text-transform: none;
        display: grid;
        grid-template-columns: 1fr 2fr;
        @media screen and (max-width: 992px) {
          grid-template-columns: 1fr;
        }
        > * {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        label {
          @media screen and (max-width:992px) {
            padding-left: 10%;
          }
        }
        span {
          margin: .75rem;
          input,
          select {
            width: 100%;
            color: rgba(0,0,0, .7);
            font-family: "Oswald - Regular Italic";
            font-size: 18px;
            font-weight: 400;
            padding:  .5rem 1rem;
            border: 2px solid #000;
            border-radius: 20px;
            background: transparent;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            &#name {
              width: 30%;
            }
            &#last_name {
              width: 45%;
            }
          }
        }
        .shopping {
          &__list {
            flex-direction: column;
            align-items: normal;
            list-style: none;
            padding-left: 0;

            max-height: 70vh;
            display: block;
            overflow-x: hidden;
            padding-right: 10%;

            &::-webkit-scrollbar {
              width: 10px;
            }
            &::-webkit-scrollbar-track {
              box-shadow: inset 0 0 5px grey;
              border-radius: 10px;
            }
            &::-webkit-scrollbar-thumb {
              background: #ff4d4d;
              border-radius: 10px;
            }
            &::-webkit-scrollbar-thumb:hover {
              background: #b30000;
            }
            @media screen and (max-width: 1200px) {
              padding: 0 5%;
              max-height: initial;
            }
            &--col {
              &:first-child {
                display: flex;
                align-items: center;
                min-width: 40%;
                @media screen and (max-width: 992px) {
                  grid-column-start: 1;
                  grid-column-end: 2;
                }
              }
              &:nth-child(2) {
                @media screen and (max-width: 992px) {
                  grid-column-start: 1;
                  grid-column-end: 1;
                }
              }
              &:nth-child(3) {
                min-width: 20%;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                @media screen and (max-width: 992px) {
                  grid-column-start: 2;
                  grid-column-end: 2;
                }
              }
            }
            &--item {
              display: flex;
              align-items: center;
              justify-content: space-between;
              position: relative;
              @media screen and (max-width: 992px) {
                display: grid;
              }
            }
            &--img {
              background-repeat: no-repeat;
              background-position: center;
              background-size: contain;
              width: 58px;
              height: 58px;
              &-wrapper {
                border: 2px solid #000;
                border-radius: 10px;
                width: 64px;
                height: 64px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: .5em .5em .5em 0;
              }
            }
            &--phone,
            &--model {
              font-family: "Oswald - Regular Italic";
              text-transform: capitalize;
              font-style: normal;
              font-size: 1rem;
              font-weight: bold;
            }
            &--price {
              font-family: "Oswald - Regular Italic";
              font-size: 1.5rem;
              font-weight: bold;
              padding-right: 1.75rem;
              min-width: 25%;
              &-text {
                margin: 0;
                font-weight: normal;
                font-size: 1.25rem;
              }
              &-info {
                color: #b2b2b2;
                font-size: .75rem;
              }
            }
            &--count {
              &-wrapper {
                display: flex;
                align-items: center;
              }
              span {
                font-family: "Oswald - Regular Italic";
                font-size: 1.25rem;
                width: 2.25rem;
                text-align: center;
                border:none;
                background: transparent;
              }
              button {
                background: transparent;
                /*border: 1px solid #000;*/
                border-radius: 50%;
                width: 26px;
                height: 26px;
                border: 1px solid #ededed;
                /*border-radius: 50px;*/
                cursor: pointer;
                outline: none;
                background-repeat: no-repeat;
                background-position: center;
                background-size: 85%;
              }
            }
            &--delete {
              position: absolute;
              top: 0;
              right: 0px;
              button {
                transform: rotate(45deg);
                background-color: #ff0000;
                width: 15px;
                height: 15px;
                border-color: #000;
              }
            }
          }
        }
      }
    }
  }

  .modal_response {
    z-index: 999;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,.5);
    /*padding: 5%;*/
    &__content {
      width: 60%;
      background-color: #000;
      border-radius: 30px;
      position: relative;
      box-shadow: 0 0 10.5rem 1.5rem rgba(255,255,255,0.75);
      &--wrapper {
        overflow-x: auto;
        min-height: 50vh;
        max-height: 90vh;
        display: flex;
      }
      .col-6 {
        width: 50%;
        white-space: pre-line;
        padding: 3% 6%;
      }
      .closeModal {
        position: absolute;
        background: transparent;
        border: none;
        transform: rotate(45deg);
        width: 64px;
        height: 64px;
        top: .5rem;
        right: .5rem;
        cursor: pointer;
        outline: none;
        svg {
          transition: all ease-in-out .5s;
        }
        &:hover {
          svg {
            stroke: #ff0000;
          }
        }
      }
    }
  }
</style>



