import Vue from 'vue';

let storelist = [];

function getStore () {
  if(localStorage.getItem('store')) {
    storelist = JSON.parse( localStorage.getItem('store') );
    storelist =  Vue.lodash.uniqWith(storelist, Vue.lodash.isEqual);
    return storelist;
  } else {
    return false;
  }
};

function getStoreLength() {
  getStore();
  if(localStorage.getItem('store')) {
    return storelist.length;
  } else {
    return 0;
  }
};

function refreshStorage() {
  localStorage.removeItem('store');
  localStorage.setItem("store", JSON.stringify(storelist) );
};

function setItemToStore(item, model) {
  // console.log( item + ' ' + model );
  let arr = { "id": item, "model": model };
  storelist.push( arr );
  // console.log( storelist )
  refreshStorage();
};

function deleteElement(id, model) {
  storelist = storelist.filter(function (item) {
    if( item.id != id || item.model != model ) {
      return item;
    }
  });
  // console.log( storelist );

  refreshStorage();
};

export default {
  getStoreLength,
  setItemToStore,
  deleteElement,
  getStore
};



